export interface ApiResponse<T = any> {
  success: boolean;
  data?: T;
  error?: string;
  message?: string;
  timestamp: string;
}

export const successResponse = <T>(data: T, message?: string): ApiResponse<T> => ({
  success: true,
  data,
  message,
  timestamp: new Date().toISOString(),
});

export const errorResponse = (error: string, message?: string): ApiResponse => ({
  success: false,
  error,
  message,
  timestamp: new Date().toISOString(),
});

export const validationErrorResponse = (errors: Array<{ field: string; message: string }>): ApiResponse => ({
  success: false,
  error: 'Validation Error',
  data: { errors },
  timestamp: new Date().toISOString(),
});
