// Template types
export type EmailTemplateType = keyof typeof emailTemplates;

// Template data interfaces
export interface OTPTemplateData {
  name: string;
  otp: string;
}

export interface WelcomeTemplateData {
  name: string;
}

// Template data union type
export type TemplateData = OTPTemplateData | WelcomeTemplateData;

// Simple template renderer that replaces {{variable}} with values
// This is Workers-compatible (no eval() or Function() constructor)
export function renderTemplate(template: string, data: Record<string, any>): string {
  return template.replace(/\{\{(\w+)\}\}/g, (match, key) => {
    return data[key] !== undefined ? String(data[key]) : match;
  });
}
