import nodemailer from 'nodemailer';
import { emailTemplates, renderTemplate } from './email-templates';

// Email configuration
interface EmailConfig {
  host: string;
  port: number;
  service: string;
  user: string;
  password: string;
}

// Get email configuration from environment
const getEmailConfig = (env?: any): EmailConfig => {
  const config = {
    host: env?.SMTP_HOST || process.env.SMTP_HOST || 'smtp.gmail.com',
    port: parseInt(env?.SMTP_PORT || process.env.SMTP_PORT || '465'),
    service: env?.SMTP_SERVICE || process.env.SMTP_SERVICE || 'gmail',
    user: env?.SMTP_USER || process.env.SMTP_USER,
    password: env?.SMTP_PASSWORD || process.env.SMTP_PASSWORD,
  };

  if (!config.user || !config.password) {
    throw new Error('SMTP_USER and SMTP_PASSWORD environment variables are required');
  }

  return config;
};

// Create transporter
const createTransporter = (config: EmailConfig) => {
  return nodemailer.createTransport({
    host: config.host,
    port: config.port,
    secure: config.port === 465, // true for 465, false for other ports
    service: config.service,
    auth: {
      user: config.user,
      pass: config.password,
    },
  });
};

// Render email template with base layout
const renderEmailTemplate = (templateType: keyof typeof emailTemplates, data: any): string => {
  const templateContent = emailTemplates[templateType];
  const renderedContent = renderTemplate(templateContent, data);

  // Wrap in base template
  return renderTemplate(emailTemplates.base, {
    title: getEmailTitle(templateType),
    content: renderedContent,
  });
};

// Get email title based on template type
const getEmailTitle = (templateType: keyof typeof emailTemplates): string => {
  switch (templateType) {
    case 'otpVerification':
      return 'Email Verification - Hamsoya';
    case 'passwordReset':
      return 'Password Reset - Hamsoya';
    case 'welcome':
      return 'Welcome to Hamsoya!';
    default:
      return 'Hamsoya';
  }
};

// Email sending interface
interface SendEmailOptions {
  to: string;
  subject: string;
  template: keyof typeof emailTemplates;
  data: any;
}

// Send email function
export const sendEmail = async (options: SendEmailOptions, env?: any): Promise<void> => {
  try {
    const config = getEmailConfig(env);
    const transporter = createTransporter(config);

    // Render template
    const html = renderEmailTemplate(options.template, options.data);

    // Send email
    await transporter.sendMail({
      from: `"Hamsoya" <${config.user}>`,
      to: options.to,
      subject: options.subject,
      html,
    });

    console.log(`✅ Email sent successfully to ${options.to}`);
  } catch (error) {
    console.error('❌ Failed to send email:', error);
    throw new Error('Failed to send email');
  }
};

// Predefined email functions
export const sendOTPVerificationEmail = async (
  email: string,
  name: string,
  otp: string,
  env?: any
): Promise<void> => {
  await sendEmail(
    {
      to: email,
      subject: 'Verify Your Email - Hamsoya',
      template: 'otpVerification',
      data: { name, otp },
    },
    env
  );
};

